import React from "react";
import ContactUsSection from "../sections/ContactUsSection";
import ConsultancySection from "../sections/ConsultancySection";
import PartnershipSection from "../sections/PartnershipSection";
import jumbo from "../assets/jumbo-consultancy.jpg";
import "../framework/site.scss";

export default class ConsultancyContent extends React.PureComponent {
  render() {
    return (
      <main>
          <section className="intro">
              <img src={jumbo} alt="Consultancy"/>
          </section>
          <ConsultancySection showHeader={false} />
          <ContactUsSection />
          <PartnershipSection />
      </main>
    );
  }
}