import React from "react";
import {InternalLink} from "../framework/Links";
import {RouteID} from "../application/Routes";
import "../framework/site.scss";

export interface ConsultancySectionProps {
    showHeader: boolean
}

export default class ConsultancySection extends React.PureComponent<ConsultancySectionProps> {
  render() {
    return (
        <section className="content" id="consultancy">
            {this.props.showHeader ? <h5>Consultancy</h5> : <></>}
            <p>Consider hiring a consultant as the most effective and efficient way to supplement
                your team, identify and fix a problem in the solution, to influence or catalyst the change
                or to let your team adopt and learn a new technology,
                or simply do the dirty job and let your team focus on the priorities.</p>
            <p>We know the technology.</p><p>We have the right people.</p>
            <p><InternalLink routeID={RouteID.ROUTE_ID_CONTACT}>Contact us today</InternalLink></p>
        </section>
    );
  }
}